import { driver } from 'driver.js';
import 'driver.js/dist/driver.css';
import type { InteractiveTutorial, TutorialStep } from '@/types/training';

export class TutorialDriver {
  private driverInstance: any;
  private currentStepIndex = 0;
  private tutorial: InteractiveTutorial;

  constructor(tutorial: InteractiveTutorial) {
    this.tutorial = tutorial;
  }

  start(): void {
    const tourSteps: any[] = this.tutorial.steps
      .filter(step => step.type === 'tour' || step.type === 'task')
      .map((step, idx) => ({
        element: step.tour?.selector || `[data-tutorial-step="${step.id}"]`,
        popover: {
          title: step.title.es || step.title.en,
          description: step.tour?.content?.es || step.description.es,
          popoverSide: step.tour?.position || 'bottom',
          align: 'start',
        },
        onNext: () => this.handleStepNext(idx),
        onPrevious: () => this.handleStepPrev(idx),
      }));

    this.driverInstance = driver({
      steps: tourSteps,
      showProgress: true,
      overlayColor: 'rgba(0, 0, 0, 0.8)',
      overlayOpacity: 0.5,
      showButtons: ['next', 'previous', 'close'],
      allowClose: true,
      onDestroyStarted: () => this.handleEnd(),
    });

    this.driverInstance.drive();
  }

  private handleStepNext(stepIdx: number): void {
    this.currentStepIndex = stepIdx + 1;
    // Log progress
    console.log(`Tutorial progress: ${this.currentStepIndex}/${this.tutorial.steps.length}`);
  }

  private handleStepPrev(stepIdx: number): void {
    this.currentStepIndex = Math.max(0, stepIdx - 1);
  }

  private handleEnd(): void {
    this.driverInstance.destroy();
  }

  destroy(): void {
    if (this.driverInstance) {
      this.driverInstance.destroy();
    }
  }
}

export function createTutorialDriver(tutorial: InteractiveTutorial): TutorialDriver {
  return new TutorialDriver(tutorial);
}
