'use client';

import { useEffect, useState } from 'react';
import { ChevronRight, CheckCircle, BookOpen } from 'lucide-react';
import { createTutorialDriver } from '@/lib/tutorials/driver';
import type { InteractiveTutorial, TutorialProgress } from '@/types/training';

interface Props {
  tutorial: InteractiveTutorial;
  onComplete?: () => void;
}

export function InteractiveTutorial({ tutorial, onComplete }: Props) {
  const [progress, setProgress] = useState<TutorialProgress | null>(null);
  const [isRunning, setIsRunning] = useState(false);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    const fetchProgress = async () => {
      try {
        const res = await fetch(`/api/partners/tutorials/${tutorial.id}/progress`);
        const data = await res.json();
        setProgress(data.progress);
      } catch (error) {
        console.error('Failed to fetch progress:', error);
      }
    };

    fetchProgress();
  }, [tutorial.id]);

  const handleStartTutorial = async () => {
    setLoading(true);
    setIsRunning(true);

    // Create and start driver
    const driver = createTutorialDriver(tutorial);
    driver.start();

    // Update progress
    try {
      await fetch(`/api/partners/tutorials/${tutorial.id}/progress`, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          status: 'in_progress',
          currentStep: 0,
        }),
      });
    } catch (error) {
      console.error('Failed to update progress:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleCompleteTutorial = async () => {
    try {
      await fetch(`/api/partners/tutorials/${tutorial.id}/progress`, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          status: 'completed',
          currentStep: tutorial.steps.length,
          completedSteps: tutorial.steps.map((_, i) => i),
        }),
      });

      const res = await fetch(`/api/partners/tutorials/${tutorial.id}/progress`);
      const data = await res.json();
      setProgress(data.progress);

      if (onComplete) onComplete();
    } catch (error) {
      console.error('Failed to complete tutorial:', error);
    }
  };

  const completionPercent = progress
    ? (progress.completedSteps.length / tutorial.steps.length) * 100
    : 0;

  return (
    <div className="bg-dark-surface border border-white/10 rounded-2xl p-6">
      <div className="flex items-start justify-between mb-4">
        <div>
          <h3 className="text-xl font-bold mb-2">{tutorial.title.es || tutorial.title.en}</h3>
          <p className="text-neutral text-sm">{tutorial.description.es || tutorial.description.en}</p>
        </div>
        <span className={`px-3 py-1 rounded text-xs font-medium ${
          tutorial.difficulty === 'beginner'
            ? 'bg-green-500/20 text-green-200'
            : tutorial.difficulty === 'intermediate'
              ? 'bg-yellow-500/20 text-yellow-200'
              : 'bg-red-500/20 text-red-200'
        }`}>
          {tutorial.difficulty}
        </span>
      </div>

      {/* Progress Bar */}
      {progress && progress.status !== 'not_started' && (
        <div className="mb-4">
          <div className="flex justify-between text-sm mb-2">
            <span className="text-neutral">Progreso</span>
            <span className="font-medium">{Math.round(completionPercent)}%</span>
          </div>
          <div className="h-2 bg-white/10 rounded-full overflow-hidden">
            <div
              className="h-full bg-primary transition-all"
              style={{ width: `${completionPercent}%` }}
            />
          </div>
        </div>
      )}

      {/* Tutorial Info */}
      <div className="grid grid-cols-3 gap-3 mb-6 text-sm">
        <div className="bg-white/5 rounded p-3">
          <span className="text-neutral block text-xs mb-1">Duración</span>
          <span className="font-medium">{tutorial.duration} min</span>
        </div>
        <div className="bg-white/5 rounded p-3">
          <span className="text-neutral block text-xs mb-1">Pasos</span>
          <span className="font-medium">{tutorial.steps.length}</span>
        </div>
        <div className="bg-white/5 rounded p-3">
          <span className="text-neutral block text-xs mb-1">Tipo</span>
          <span className="font-medium capitalize">{tutorial.type}</span>
        </div>
      </div>

      {/* Actions */}
      <div className="flex gap-3">
        <button
          onClick={handleStartTutorial}
          disabled={loading || (progress?.status === 'completed')}
          className="flex-1 bg-primary hover:bg-primary-dark disabled:opacity-50 text-white font-medium py-2 rounded-lg transition flex items-center justify-center gap-2"
        >
          {progress?.status === 'completed' ? (
            <>
              <CheckCircle className="w-5 h-5" />
              Completado
            </>
          ) : (
            <>
              <BookOpen className="w-5 h-5" />
              {progress?.status === 'in_progress' ? 'Continuar' : 'Comenzar'} Tutorial
            </>
          )}
        </button>

        {progress?.status === 'in_progress' && (
          <button
            onClick={handleCompleteTutorial}
            className="px-6 bg-green-500/20 hover:bg-green-500/30 text-green-200 font-medium py-2 rounded-lg transition flex items-center gap-2"
          >
            Marcar como Completado
            <ChevronRight className="w-4 h-4" />
          </button>
        )}
      </div>
    </div>
  );
}
