'use client';

import { useEffect, useState } from 'react';
import { InteractiveTutorial } from '@/components/portal/training/InteractiveTutorial';
import { SEED_TUTORIALS } from '@/lib/tutorials/seed';
import type { InteractiveTutorial as ITutorial } from '@/types/training';

export default function TutorialsCenterPage() {
  const [tutorials, setTutorials] = useState<ITutorial[]>(SEED_TUTORIALS);
  const [category, setCategory] = useState<string>('all');
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    const fetchTutorials = async () => {
      try {
        const res = await fetch('/api/partners/tutorials');
        if (res.ok) {
          const data = await res.json();
          if (data.tutorials && data.tutorials.length > 0) {
            setTutorials(data.tutorials);
          }
        }
      } catch (error) {
        console.error('Failed to fetch tutorials from API, using seed data:', error);
        // Keep seed data as fallback
      }
    };

    fetchTutorials();
  }, []);

  const filtered =
    category === 'all'
      ? tutorials
      : tutorials.filter(t => t.category === category);

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="bg-dark-surface border border-white/10 rounded-2xl p-6">
        <h1 className="text-3xl font-bold mb-2">Centro de Tutoriales Interactivos</h1>
        <p className="text-neutral">
          Aprende SovraGov a través de tutoriales prácticos paso-a-paso
        </p>
      </div>

      {/* Filter */}
      <div className="flex gap-2 overflow-x-auto pb-2">
        {['all', 'catalog', 'form', 'stage', 'appointment', 'payment', 'inspection', 'emission'].map(cat => (
          <button
            key={cat}
            onClick={() => setCategory(cat)}
            className={`px-4 py-2 rounded-full whitespace-nowrap transition ${
              category === cat
                ? 'bg-primary text-white'
                : 'bg-white/10 hover:bg-white/20 text-neutral'
            }`}
          >
            {cat === 'all' ? 'Todos' : cat}
          </button>
        ))}
      </div>

      {/* Tutorials Grid */}
      <div className="grid md:grid-cols-2 gap-6">
        {filtered.map(tutorial => (
          <InteractiveTutorial key={tutorial.id} tutorial={tutorial} />
        ))}
      </div>

      {filtered.length === 0 && (
        <div className="text-center py-12">
          <p className="text-neutral">No hay tutoriales en esta categoría</p>
        </div>
      )}
    </div>
  );
}
